@use "../../styles/partials/mixins" as *;

.contact {
  margin: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @include tablet {
    margin-bottom: 4rem;
  }
  &__container {
    width: 100%;
    background-color: #f9f9f9;
    padding: 2rem;
    border-radius: 2rem;
    @include tablet {
      width: 28rem;
    }
  }

  &__intro {
    text-align: center;
  }

  &__heading {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 1rem;
  }

  &__description {
    font-size: 1.2rem;
    color: #555;
    text-align: center;
    margin-bottom: 2rem;
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
  }

  &__label {
    font-size: 1rem;
    font-weight: bold;
    color: #333;
  }

  &__input,
  &__textarea {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
  }

  &__textarea {
    resize: none;
    height: 100px;
  }

  &__button {
    padding: 0.75rem;
    background-color: #5a3e79;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    border-radius: 2rem;
    width: 10rem;
    margin: 0 auto;
    margin-top: 1rem;
    &:hover {
      background-color: #c8b0f1;
    }
  }

  &__info {
    text-align: center;

    &__subheading {
      font-size: 1.5rem;
      font-weight: bold;
    }

    &__text {
      font-size: 1rem;
      color: #555;
    }
  }
}
