@use "../../styles/partials/mixins" as *;

.header {
  display: flex;
  flex-direction: column;
  background-color: #d7caf1;
  color: white;
  position: relative;

  // Container for Logo and Menu Icon
  &__container {
    display: flex;
    align-items: center;
    z-index: 10;
  }

  &__wrapper {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;

    @include tablet {
      padding: 1rem 2rem;
    }

    @include desktop {
      justify-content: center;
      position: relative;
      padding: 1rem 5rem;
    }

    & > .header__menu-icon {
      font-size: 2rem;
      cursor: pointer;

      @include desktop {
        position: absolute;
        left: 1rem;
        display: none; /* Hide on desktop */
      }
    }

    & > .header__shopping-bag {
      font-size: 2rem;
      cursor: pointer;

      @include desktop {
        position: absolute;
        right: 2rem;
        font-size: 3rem;
      }
    }
  }

  // Logo
  &__logo {
    width: 8rem;
    height: 8rem;
    display: block;

    @include desktop {
      width: 12rem;
      height: 12rem;
      margin: 0 auto;
    }
  }

  // Navigation Menu
  &__nav {
    display: none;
    align-items: center;
    font-family: "Nunito-Bold", sans-serif;
    font-weight: 700;
    @include desktop {
      justify-content: center;
    }

    &--open {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      padding: 2rem;
      background-color: #d7caf1;
      width: 100%;
    }

    &-list {
      list-style: none;
      display: flex;
      flex-direction: column;
      gap: 1.5rem;

      @include desktop {
        flex-direction: row;
        justify-content: center;
        gap: 6rem;
        background-color: transparent;
        font-size: 1.25rem;
      }

      &__item {
        a {
          display: flex;
          align-items: center; /* Align icon and text vertically */
          gap: 0.5rem; /* Add spacing between icon and text */
          text-decoration: none;
          color: white;
          font-size: 1.2rem;

          @include desktop {
            font-size: 1.5rem;
            color: #333;
          }

          &:hover {
            color: #00bcd4;
          }

          // Icon styling
          svg {
            font-size: 1.5rem;

            @include desktop {
              font-size: 2rem;
            }
          }
        }
      }
    }

    @include desktop {
      display: flex;
      flex-direction: row;
    }
  }
}
